import Report from "../Report/Report";

import revenue from "../../assets/icons/revenue.png";
import expenses from "../../assets/icons/expenses.png";
import "./finances.css";

const Finances = () => {
  return (
    <>
      <section className="finances bg-white">
        <div className="container">
          <h2>Students For Liberty</h2>
          <h3>Finances</h3>
          <div className="finances-content-wrapper">
            <div className="finance-card">
              <h4>Revenue</h4>
              <div className="finance-card-content-wrapper">
                <div className="finances-card-header bg-yellow">
                  <span className="number-one">$5,196,568</span>
                  <img src={revenue} />
                </div>
              </div>
              <div className="finance-card-chart-descr">
                <ul>
                  <li>
                    <p>Contributions and grants</p>
                    <span class="percentage">89.77%</span>
                  </li>
                  <li>
                    <p>Events sponsorship/registration</p>
                    <span class="percentage">5.77%</span>
                  </li>
                  <li>
                    <p>Investment/other incomeM</p>
                    <span class="percentage">2.84%</span>
                  </li>
                  <li>
                    <p>Inkind</p> <span class="percentage">1.62%</span>
                  </li>
                </ul>
              </div>
            </div>
            <div className="finance-card">
              <h4>Expenses</h4>
              <div className="finance-card-content-wrapper ">
                <div className="finances-card-header bg-yellow">
                  <span className="number-one">$7,004,850</span>
                  <img src={expenses} />
                </div>
              </div>
              <div className="finance-card-chart-descr">
                <ul>
                  <li>
                    <p>Contributions and grants</p>
                    <span class="percentage">89.77%</span>
                  </li>
                  <li>
                    <p>Events sponsorship/registration</p>
                    <span class="percentage">5.77%</span>
                  </li>
                  <li>
                    <p>Investment/other incomeM</p>
                    <span class="percentage">2.84%</span>
                  </li>
                  <li>
                    <p>Inkind</p> <span class="percentage">1.62%</span>
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </div>
      </section>
      <Report />
    </>
  );
};

export default Finances;
