import { useRef } from "react";
import {
  TwitterShareButton,
  FacebookShareButton,
  EmailShareButton,
  LinkedinShareButton,
} from "react-share";
import { XIcon, FacebookIcon, EmailIcon, LinkedinIcon } from "react-share";

import DonateBtn from "../DonateBtn/DonateBtn";
import image from "../../assets/images/img-placeholder.webp";
import copyLinkIcon from "../../assets/icons/copy-link.svg";
import "./footer.css";

const Footer = () => {
  const shareUrl = "https://studentsforliberty.org/";

  const linkRef = useRef();

  function copyCurrentUrl() {
    const url = window.location.href;
    navigator.clipboard.writeText(url);
    linkRef.current.classList.add("clicked");
    setTimeout(() => {
      linkRef.current.classList.remove("clicked");
    }, 2000);
  }
  return (
    <footer className="footer bg-grey">
      <div className="container">
        <div className="footer-first-wrapper">
          <h3>Invest in Liberty</h3>
          <DonateBtn />
          <a href="#" className="sfl-website">
            Students For Liberty ›
          </a>
        </div>
        <div className="footer-second-wrapper">
          <div className="footer-images-wrapper">
            <img src={image} />
            <img src={image} />
            <img src={image} />
            <img src={image} />
          </div>
          <div className="share-report">
            <p>Share Report:</p>
            <div className="footer-social-icons-wrapper">
              <FacebookShareButton url={shareUrl}>
                <FacebookIcon size={32} />
              </FacebookShareButton>
              <TwitterShareButton url={shareUrl}>
                <XIcon size={32} />
              </TwitterShareButton>
              <EmailShareButton url={shareUrl}>
                <EmailIcon size={32} />
              </EmailShareButton>
              <a
                ref={linkRef}
                onClick={copyCurrentUrl}
                data-message="Page link copied to clipboard!"
                className="copy-to-clipboard"
              >
                <img src={copyLinkIcon} />
              </a>
            </div>
          </div>
        </div>
      </div>
    </footer>
  );
};

export default Footer;
