import { useEffect, useRef } from "react";
import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import Splitting from "splitting";
import { animateTitle1 } from "../../helpers/animateTitle";
import CountUp from "react-countup";
import "./year-in-numbers.css";

import firstIcon from "../../assets/icons/cummulative-icon.png";
import secondIcon from "../../assets/icons/total-reach-icon.png";
import therdIcon from "../../assets/icons/events-participants.png";
import fourIcon from "../../assets/icons/people-trained.png";
import fiveIcon from "../../assets/icons/students-volunteers.png";
import sixIcon from "../../assets/icons/globe-events.png";

const YearInNumbers = () => {
  const titleRef = useRef();

  useEffect(() => {
    gsap.registerPlugin(ScrollTrigger);
    Splitting();
    animateTitle1(titleRef.current);
  }, []);

  return (
    <section className="year-in-number bg-white">
      <div className="container">
        <h2>Year in numbers</h2>
        <h3 ref={titleRef} data-splitting style={{ width: "80%" }}>
          The largest international pro-liberty student organization in the
          world!
        </h3>
        <div className="content-wrapper">
          <div className="dashboard-grid">
            <div className="card large-card border-radius-20">
              <CountUp
                start={0}
                end={1544974}
                delay={0}
                duration={10}
                separator=","
              >
                {({ countUpRef }) => (
                  <div>
                    <span className="number-one" ref={countUpRef} />
                  </div>
                )}
              </CountUp>

              <p>Cumulative event attendees since FY11</p>
              <img src={firstIcon} className="year-in-number__icon" />
            </div>
            <div className="card medium-card border-radius-20">
              <CountUp
                start={0}
                end={229660}
                delay={0}
                duration={5}
                separator=","
              >
                {({ countUpRef }) => (
                  <div>
                    <span className="number-one" ref={countUpRef} />
                  </div>
                )}
              </CountUp>
              <p>Total reach in FY24</p>
              <img src={secondIcon} className="year-in-number__icon" />
            </div>
            <div className="card small-card border-radius-20">
              <span className="number-one">$14M</span>
              <p>
                Economic
                <br /> Impact
              </p>
            </div>
            <div className="card small-card border-radius-20">
              <span className="number-one">241%</span>
              <p>
                Return on investment:
                <br /> $1 = $2.41 of impact
              </p>
            </div>
            <div className="card small-card border-radius-20">
              <span className="number-one">873h</span>
              <p>
                Hours training more <br /> than ever
              </p>
            </div>
          </div>
          <h3 style={{ textAlign: "center" }}>
            Every dollar of donor input produces $2.41 of impact.
          </h3>
          <div className="impact-grid">
            <div className="impact-item border-radius-20">
              <div className="impact-icon"></div>
              <img src={therdIcon} className="year-in-number__impact-icon" />
              <span className="number-two">81,428</span>
              <p>Events participants</p>
            </div>
            <div className="impact-item border-radius-20">
              <div className="impact-icon"></div>
              <img src={fourIcon} className="year-in-number__impact-icon" />
              <span className="number-two">11,918</span>
              <p>People Trained</p>
            </div>
            <div className="impact-item border-radius-20">
              <div className="impact-icon"></div>
              <img src={fiveIcon} className="year-in-number__impact-icon" />
              <span className="number-two">2,500</span>
              <p>Students Volunteers</p>
            </div>
            <div className="impact-item border-radius-20">
              <div className="impact-icon"></div>
              <img src={sixIcon} className="year-in-number__impact-icon" />
              <span className="number-two">3,807</span>
              <p>Events around the Globe</p>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default YearInNumbers;
