import { useRef, useState, useEffect } from "react";

import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import Splitting from "splitting";
import { animateTitle1 } from "../../helpers/animateTitle";

import Btn from "../Btn/Btn";
import globe from "../../assets/icons/globe-events.png";

import image from "../../assets/images/img-placeholder.webp";
import pepperSpray from "../../assets/images/pepper-spray.webp";
import freeSpech from "../../assets/images/free-spech.webp";
import lci from "../../assets/images/lci.webp";
import "./global-presence.css";

const GlobalPresence = () => {
  const titleRef = useRef();

  useEffect(() => {
    gsap.registerPlugin(ScrollTrigger);
    Splitting();
    animateTitle1(titleRef.current);
  }, []);

  const [activeTab, setActiveTab] = useState("North America");

  const tabs = {
    "North America":
      "SFL chapters champion student rights, free speech, and host LibertyCon in D.C.",
    "Asia Pacific":
      "SFL chapters champion student rights, free speech, and host LibertyCon in D.C.",
    Europe:
      "SFL chapters champion student rights, free speech, and host LibertyCon in D.C.",
    Africa:
      "SFL chapters champion student rights, free speech, and host LibertyCon in D.C.",
    Eslibertad:
      "SFL chapters champion student rights, free speech, and host LibertyCon in D.C.",
    MENA: "SFL chapters champion student rights, free speech, and host LibertyCon in D.C.",
    "South Asia":
      "SFL chapters champion student rights, free speech, and host LibertyCon in D.C.",
    Brasil:
      "SFL chapters champion student rights, free speech, and host LibertyCon in D.C.",
  };
  const tabsContentRef = useRef({
    "North America": {
      location: "Tennessee",
      name: "Pepper Spray Persuasion",
      description:
        "Students at the University of Tennessee fought for the right to carry pepper spray on campus for self-defense, resulting in a significant policy change.",
      image: pepperSpray,
      location2: "New Hampshire",
      name2: "Free Speech from the Heart and the Dartmouth",
      description2:
        "A successful summit at Dartmouth University highlighted the importance of free speech and raised a record-breaking amount for SFL’s North America region.",
      image2: freeSpech,
      location3: "WASHINGTON, DC",
      name3: "LibertyCon International Hits New Heights",
      description3:
        "LibertyCon 2024 in Washington, D.C., brought together 817 attendees from 51 countries, showcasing an inspiring vision for global liberty.",
      image3: lci,
    },
    "Asia Pacific": {
      name: "Dartmouth Free Speech Summit: Students Champion Open Dialogue",
      image: image,
      name2: "LibertyCon International was a major success",
      image2: image,
      name3:
        "Overturning of a ban on pepper spray on campus in TN in order to protect women from rape",
      image3: image,
    },
    Europe: {
      name: "Overturning of a ban on pepper spray on campus in TN in order to protect women from rape",
      image: image,
      name2: "LibertyCon International was a major success",
      image2: image,
      name3: "Dartmouth Free Speech Summit: Students Champion Open Dialogue",
      image3: image,
    },
    Africa: {
      name: "Overturning of a ban on pepper spray on campus in TN in order to protect women from rape",
      image: image,
      name2: "LibertyCon International was a major success",
      image2: image,
      name3: "Dartmouth Free Speech Summit: Students Champion Open Dialogue",
      image3: image,
    },
    Eslibertad: {
      name: "Overturning of a ban on pepper spray on campus in TN in order to protect women from rape",
      image: image,
      name2: "LibertyCon International was a major success",
      image2: image,
      name3: "Dartmouth Free Speech Summit: Students Champion Open Dialogue",
      image3: image,
    },
    MENA: {
      name: "Overturning of a ban on pepper spray on campus in TN in order to protect women from rape",
      image: image,
      name2: "LibertyCon International was a major success",
      image2: image,
      name3: "Dartmouth Free Speech Summit: Students Champion Open Dialogue",
      image3: image,
    },
    "South Asia": {
      name: "Overturning of a ban on pepper spray on campus in TN in order to protect women from rape",
      image: image,
      name2: "LibertyCon International was a major success",
      image2: image,
      name3: "Dartmouth Free Speech Summit: Students Champion Open Dialogue",
      image3: image,
    },
    Brasil: {
      name: "Overturning of a ban on pepper spray on campus in TN in order to protect women from rape",
      image: image,
      name2: "LibertyCon International was a major success",
      image2: image,
      name3: "Dartmouth Free Speech Summit: Students Champion Open Dialogue",
      image3: image,
    },
  });

  const activeTabContent = tabsContentRef.current[activeTab];

  return (
    <section className="global-presence bg-grey">
      <div className="container">
        <h2>around the globe</h2>
        <h3 ref={titleRef} data-splitting>
          Transforming the World:
          <br /> Our Latest Stories by Regions
        </h3>
        <div className="tabs-wrapper">
          <ul className="tabs">
            {Object.entries(tabs).map(([key, value]) => (
              <li
                key={key}
                onClick={() => setActiveTab(key)}
                className={activeTab === key ? "active" : ""}
              >
                <span>{key}</span>
                <p>{value}</p>
              </li>
            ))}
            <div>
              <p>SFL is present in</p>
              <span>109</span>
              <p>countries around the globe</p>
              <img src={globe} />
            </div>
          </ul>

          <div className="tab-content">
            <div className="tab-featured-content border-radius-20">
              <div className="tab-featured-content-img-wrapper">
                <img src={activeTabContent.image} />
              </div>
              <div className="tab-featured-content-wrapper">
                <span>{activeTabContent.location}</span>
                <h4>{activeTabContent.name}</h4>
                <p>{activeTabContent.description}</p>
              </div>
            </div>
            <div className="tab-secondary-content">
              <div className="border-radius-20 tab-secondary-content-wrapper">
                <div className="tab-secondary-content-img-wrapper">
                  <img src={activeTabContent.image2} />
                </div>

                <span>{activeTabContent.location2}</span>
                <h4>{activeTabContent.name2}</h4>
                <p>{activeTabContent.description2}</p>
              </div>
              <div className="border-radius-20 tab-secondary-content-wrapper">
                <div className="tab-secondary-content-img-wrapper">
                  <img src={activeTabContent.image3} />
                </div>

                <span>{activeTabContent.location3}</span>
                <h4>{activeTabContent.name3}</h4>
                <p>{activeTabContent.description3}</p>
              </div>
            </div>
          </div>
        </div>
      </div>
      <Btn text={"More Stories"} />
    </section>
  );
};

export default GlobalPresence;
